import React, { useEffect, useRef, useState } from "react";
import Slider from "react-slick"; // 슬라이더 라이브러리 추가
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import projectsDetailStyle from "./style/projectsDetail.module.css";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
const ProjectDetail = ({ project, onBack }) => {
  // 슬라이더 설정
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: true,
  };

  const [showRoleDesc, setShowRoleDesc] = useState(true);
  const [showReview, setShowReview] = useState(true);
  const [showImprovments, setShowImprovments] = useState(true);
  const [showLearning, setShowLearning] = useState(true);
  const [showImg, setShowImg] = useState(false);

  const handleBackButton = () => {
    setShowRoleDesc(true);
    setShowReview(true);
    setShowImprovments(true);
    setShowImg(false);
    onBack();
  };

  return (
    <div className={projectsDetailStyle.projectsDetailBox}>
      <div className={projectsDetailStyle.contentContainer}>
        <h2 className={projectsDetailStyle.projectTitle}>
          {project.projectsName}
        </h2>
        <p className={projectsDetailStyle.projectPeriod}>
          {project.turnarouncd}
        </p>
        <p className={projectsDetailStyle.projectOutline}>
          {project.projectOutline}
        </p>
        <h3 className={projectsDetailStyle.subTitle}>💻 기술 스택</h3>
        <div className={projectsDetailStyle.skills}>
          {project.skills.map((icon, index) => (
            <img
              key={index}
              src={icon}
              alt="skill icon"
              className={projectsDetailStyle.skillIcon}
            />
          ))}
        </div>
        <h3
          className={projectsDetailStyle.subTitle}
          onClick={() => setShowImg((prev) => !prev)}
        >
          🖼️ Project Image{" "}
          {showImg ? (
            <span className={projectsDetailStyle.detailText}>접기 🔼</span>
          ) : (
            <span className={projectsDetailStyle.detailText}>더보기 🔽</span>
          )}
        </h3>
        {/* {showImg && (
          <div className={projectsDetailStyle.sliderCover}>
            <Slider
              {...settings}
              className={projectsDetailStyle.sliderContainer}
            >
              {project.images.map((image, index) => (
                <div key={index} className={projectsDetailStyle.imageWrapper}>
                  <img
                    src={image}
                    alt={`Project Screenshot ${index + 1}`}
                    className={projectsDetailStyle.projectImage}
                  />
                </div>
              ))}
            </Slider>
          </div>
        )} */}
        {showImg && (
          <div className={projectsDetailStyle.sliderCover}>
            <Swiper
              modules={[Autoplay, Pagination, Navigation]}
              spaceBetween={30}
              slidesPerView={1}
              autoplay={{ delay: 3000 }}
              pagination={{ clickable: true }}
              navigation
              className={projectsDetailStyle.sliderContainer}
            >
              {project.images.map((image, index) => (
                <SwiperSlide
                  key={index}
                  className={projectsDetailStyle.imageWrapper}
                >
                  <img
                    src={image}
                    alt={`Project Screenshot ${index + 1}`}
                    className={projectsDetailStyle.projectImage}
                  />
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        )}
        <h3
          className={projectsDetailStyle.subTitle}
          onClick={() => setShowRoleDesc((prev) => !prev)}
        >
          🙋‍♂️ My Role{" "}
          {showRoleDesc ? (
            <span className={projectsDetailStyle.detailText}>접기 🔼</span>
          ) : (
            <span className={projectsDetailStyle.detailText}>더보기 🔽</span>
          )}
        </h3>
        {showRoleDesc && (
          <ul className={projectsDetailStyle.descriptionList}>
            {project.roleDescription.map((desc, index) => (
              <li key={index} className={projectsDetailStyle.descriptionItem}>
                {desc}
              </li>
            ))}
          </ul>
        )}
        {project.improvements && (
          <>
            <h3
              className={projectsDetailStyle.subTitle}
              onClick={() => setShowImprovments((prev) => !prev)}
            >
              🚀 Improvements{" "}
              {showImprovments ? (
                <span className={projectsDetailStyle.detailText}>접기 🔼</span>
              ) : (
                <span className={projectsDetailStyle.detailText}>
                  더보기 🔽
                </span>
              )}
            </h3>

            {showImprovments && (
              <ul className={projectsDetailStyle.descriptionList}>
                {project.improvements.map((rev, index) => (
                  <li
                    key={index}
                    className={projectsDetailStyle.descriptionItem}
                  >
                    {rev}
                  </li>
                ))}
              </ul>
            )}
          </>
        )}
        {project.review && (
          <>
            <h3
              className={projectsDetailStyle.subTitle}
              onClick={() => setShowReview((prev) => !prev)}
            >
              📝 Trouble Shooting{" "}
              {showReview ? (
                <span className={projectsDetailStyle.detailText}>접기 🔼</span>
              ) : (
                <span className={projectsDetailStyle.detailText}>
                  더보기 🔽
                </span>
              )}
            </h3>
            {showReview && (
              <ul className={projectsDetailStyle.descriptionList}>
                {project.review.map((rev, index) => (
                  <li
                    key={index}
                    className={projectsDetailStyle.descriptionItem}
                  >
                    {rev}
                  </li>
                ))}
              </ul>
            )}
          </>
        )}

        {project.learning && (
          <>
            <h3
              className={projectsDetailStyle.subTitle}
              onClick={() => setShowLearning((prev) => !prev)}
            >
              💡 What I Learned{" "}
              {showLearning ? (
                <span className={projectsDetailStyle.detailText}>접기 🔼</span>
              ) : (
                <span className={projectsDetailStyle.detailText}>
                  더보기 🔽
                </span>
              )}
            </h3>
            {showLearning && (
              <ul className={projectsDetailStyle.descriptionList}>
                {project.learning.map((rev, index) => (
                  <li
                    key={index}
                    className={projectsDetailStyle.descriptionItem}
                  >
                    {rev}
                  </li>
                ))}
              </ul>
            )}
          </>
        )}
        <h3 className={projectsDetailStyle.subTitle}>🔗 관련 링크</h3>
        <div className={projectsDetailStyle.linkContainer}>
          {project.linkIcons.map((icon, index) => (
            <a
              key={index}
              href={project.link[index]}
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src={icon}
                alt="link icon"
                className={projectsDetailStyle.linkIcon}
              />
            </a>
          ))}
        </div>
      </div>
      <button
        className={projectsDetailStyle.backButton}
        onClick={handleBackButton}
      >
        뒤로가기
      </button>
    </div>
  );
};

export default ProjectDetail;
